<template>
  <div class="hmw">
    <!--  -->
    <!-- <van-loading color="#1989fa" /> -->
    <!--  -->
    <van-popup v-model="show"> 
           <p></p>
         <img :src="imrUrl" />
    </van-popup>
    <!--  -->
    <van-sticky>
      <div class="hmw-top">
        <!--  -->
        <van-icon name="arrow-left" @click="hmwJumpTo" />
        <p v-show="!hmwFlag"></p>
        <div class="hmwNav" v-show="hmwFlag">
          <span
            :class="hmwIndex == 0 ? 'hmwActiveTop' : ''"
            @click="hmwDian(0)"
            id="hmwKc"
            ></span
          >
          <span
            :class="hmwIndex == 1 ? 'hmwActiveTop' : ''"
            @click="hmwDian(1)"
            id="hmwDg"
            ></span
          >
          <span
            :class="hmwIndex == 2 ? 'hmwActiveTop' : ''"
            @click="hmwDian(2)"
            id="hmwPj"
            ></span
          >
        </div>
        <van-icon name="cluster-o" @click="showPopup" />
      </div>
    </van-sticky>

    <!--  -->
    <div class="hmw-center" style="background: #f0f2f5">
      <!--  -->
      <van-list>
        <ol>
          <!--  -->
          <li class="hmwXQ" v-if="hmwObj.teachers_list">
            <p class="hmwP1 hmwTOP1">{{ hmwObj.title }}</p>
            <p class="hmwP2">{{ hmwObj.price == 0 ? "" : hmwObj.price }}</p>
            <p class="hmwP3">
              {{ hmwObj.total_periods }} | {{ hmwObj.brows_num }}
            </p>
            <p class="hmwP3">
              {{ hmwObj.start_play_date | timefnxq }} -
              {{ hmwObj.end_play_date | timefnxq }}
            </p>
            <!--  -->
            <van-icon
              class="hmwNo"
              v-show="!hmwSc"
              name="star-o"
              @click="hmwYes"
            />
            <van-icon
              class="hmwYes"
              v-show="hmwSc"
              name="star"
              @click="hmwNo"
            />
          </li>
          <!--  -->
          <li class="hmwTD">
            <p class="hmwP1"></p>
            <div>
              <img :src="hmwObj.teachers_list[0].teacher_avatar" alt="" /><span
                >{{ hmwObj.teachers_list[0].teacher_name }}</span
              >
            </div>
          </li>
          <!--  -->
          <li class="hmwJS hmwSroll">
            <p class="hmwP1"></p>
          </li>
          <!--  -->
          <li class="hmwDG hmwSroll" style="padding-bottom: 2px">
            <p class="hmwP1"></p>
            <!--  -->
            <ul>
              <li :key="index" v-for="(item, index) in 10">
                <p>
                  <span class="hmwS1" style=""></span
                  ><span class="hmwS2"></span>
                </p>
                <p class="hmwP3">8 | 134</p>
              </li>
            </ul>
          </li>
          <!--  -->
          <li class="hmwPL hmwSroll">
            <p class="hmwP1"></p>
            <ul>
              <li :key="index" v-for="(item, index) in 10">
                <img :src="hmwObj.teachers_list[0].teacher_avatar" alt="" />
                <div class="hmwPL-center">
                  <p class="hmwCP1">
                    <span>1234566789</span
                    ><van-rate
                      :size="14"
                      void-color="#eee"
                      color="rgb(234, 122, 47)"
                      readonly
                    />
                  </p>
                  <p class="hmwCP2"></p>
                </div>
                <div class="hmwPL-right">2020-07-15 21:32</div>
              </li>
            </ul>
          </li>
        </ol>
      </van-list>
    </div>
    <!--  -->
    <van-tabbar>
      <div class="hmw-foot">
        <van-button
          v-if="hmwBtnFlag==0"
          type="primary"
          block
          @click="hmwStudyJump(1)"
          ></van-button
        >
        <van-button
          v-if="hmwBtnFlag==1"
          type="primary"
          block
          @click="hmwStudyJump(2)"
          ></van-button
        >
      </div>
    </van-tabbar>
  </div>
</template>

<script>
import QRCode from "qrcode"; // 
import { Toast } from "vant";
export default {
  //  
  props: {},
  // 
  components: {},
  // 
  data() {
    return {
      //    --
      active: 2,
      //   
      hmwFlag: false,
      // 
      hmwIndex: 0,
      //   
      hmwShow: false,
      //   
      hmwObj: JSON.parse(sessionStorage.getItem("hmwXQ")),
      // 
      hmwBtnFlag: 0,
      // 
      show: false,
      imrUrl: "", // 
      // 
      hmwSc: false,
      // 
      hmwPathFrom: JSON.parse(sessionStorage.getItem("hmwPath")),
    };
  },
  // 
  computed: {},
  // 
  watch: {},

  // 
  methods: {
    //   nav
    scrollHandle: function (e) {
      var top = e.srcElement.scrollingElement.scrollTop; // 
      if (top > 0) {
        this.hmwFlag = true;
      } else {
        this.hmwFlag = false;
      }
    },
    //   nav
    hmwDian(index) {
      // // 
      this.hmwIndex = index;
      console.log(this.hmwIndex);
      //  offsetTop
      // css 1  0  +1
      const targetOffsetTop =
        document.querySelector(`.van-list>ol>li:nth-child(${index + 3})`)
          .offsetTop - 50;
      //  offsetTop
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      //  50 
      const STEP = 50;
      // 
      if (scrollTop > targetOffsetTop) {
        // 
        smoothUp();
      } else {
        // 
        smoothDown();
      }
      // 
      function smoothDown() {
        //  scrollTop  targetOffsetTop 
        if (scrollTop < targetOffsetTop) {
          //  STEP  STEP
          // 
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          // 

          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          //  requestAnimationFrame  setInterval 
          requestAnimationFrame(smoothDown);
        }
      }
      // 
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP;
          } else {
            scrollTop = targetOffsetTop;
          }
          document.body.scrollTop = scrollTop;
          document.documentElement.scrollTop = scrollTop;
          requestAnimationFrame(smoothUp);
        }
      }
    },
    // 
    hmwStudyJump(i) {
      if(localStorage.getItem('token')){
        if(i==1){
          this.$router.push("/isbuy");
        }else{
          this.$router.push("/study");
        }
      }else{
        this.$router.push("/login");
      }
      
      document.documentElement.scrollTop = 0;
    },
    // 
    showPopup() {
      this.show = true;
      let url = location.href;
      console.log(url);
      QRCode.toDataURL(url)
      //dataimrUrl
        .then(tpian => {
          console.log(tpian);
          this.imrUrl = tpian;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 
    // 
    async hmwYes(){
      console.log(this.hmwId)
      // let {data} = await this.$axios.post('http://120.53.31.103:84/api/app/collect',{
      //   course_basis_id:this.hmwId,
      //   type: 1
      // })
      let {data} = await this.$Net.courseXQSC({
        course_basis_id:this.hmwId,
        type: 1
      })
      localStorage.setItem("hmwId",this.hmwId)
      
      // 
      if(data.code==200){
        this.hmwSc = true
        console.log(data)
        // // 
        // this.hnwGetList()
      Toast.success('')
    };
    },
    // 
    hmwNo(){
        this.hmwSc = false
      Toast.success('')
    },
    // 
    onScroll() {
      // 
      const navContents = document.querySelectorAll(".hmwSroll");
      // console.log(navContents)
      //  offsetTop
      const offsetTopArr = [];
      navContents.forEach((item) => {
        offsetTopArr.push(item.offsetTop);
      });
      //  scrollTop
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      // console.log(scrollTop)
      // 
      let navIndex = 0;
      for (let n = 0; n < offsetTopArr.length; n++) {
        //  scrollTop n offsetTop  n-1 
        // n
        if (scrollTop >= offsetTopArr[n] - 50) {
          navIndex = n;
        }
      }
      // 
      this.hmwIndex = navIndex;
      // console.log(this.hmwIndex)
      this.active = navIndex;
    },
  // 
  async hnwGetList(){
    console.log(this.hmwObj)
    let id = this.hmwObj.teachers_list[0].course_basis_id
    // 
    let {data:list} = await this.$axios.get(`http://120.53.31.103:84/api/app/courseInfo/basis_id=${id}`)
    // 
    // console.log(data)
    // console.log(list.data)
    this.hmwObjList =list.data
    // 
    this.hmwSc = this.hmwObjList.info.is_collect
    console.log(this.hmwSc+'=====')
    // id
    this.hmwSCid = this.hmwObjList.info.collect_id
    // 
    this.hmwIsBm = this.hmwObjList.info.is_join_study
    // id
    this.hmwId = id
    // 
this.hmwBtnFlag = this.hmwObj.has_buy
    console.log(this.hmwObjList)
    // console.log(this.hmwObjList.info.id)
  },
  // 
  hmwJumpTo(){
    console.log(this.hmwPathFrom)
    this.$router.push({
        path: this.hmwPathFrom.path,
        name: this.hmwPathFrom.name,
      });
    },
  },
  mounted() {
    document.documentElement.scrollTop = 0;
    window.addEventListener("scroll", this.scrollHandle); // 
    // 
    window.addEventListener("scroll", this.onScroll, false);
    // 
    this.hnwGetList();
  },
  destroy() {
    // vue
    window.removeEventListener("scroll", this.onScroll);
  },
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
html,
body,
body > div,
#app {
  height: 100%;
}
/* ----------------------------------------------------------- */
.hmw {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.hmw-top {
  height: 3rem;
  padding: 0 1rem;
  background: white;
  border-bottom: 0.01rem solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hmw-center {
  flex: 1;
  margin-bottom: 2.8rem;
}
.hmw-foot {
  width: 100%;
  height: 2.8rem;
  z-index: 99999;
}
/*  ------------------------------------------------------------------*/
/*  */

.hmw-top .van-icon {
  font-size: 1.4rem;
  /* margin-top: 0.8rem; */
  color: #646464;
}
.hmw-top p {
  font-size: 4.8vw;
  color: #595959;
}
.hmwNav {
  display: flex;
  width: 80%;
}
.hmwNav span {
  flex: 1;
  text-align: center;
  font-size: 4vw;
  font-weight: 400;
  color: #8c8c8c;
}
.hmwNav .hmwActiveTop {
  flex: 1;
  text-align: center;
  color: #333;
  font-weight: 500;
  font-size: 4.26667vw;
}
/*  -------------------------------------------------------------------------------------*/
.hmw-center ol > li {
  background: white;
  padding: 1rem;
  margin-bottom: 1rem;
}
.hmw-center ol > li > p,
.hmw-center ol > li > .hmwTOP1 {
  padding-bottom: 0.5rem;
}
/*  */
.hmw-center .hmwP1 {
  padding-bottom: 1rem;
  font-size: 4.26667vw;
}
.hmwP2 {
  color: #eb6100;
  font-size: 4.53333vw;
}
.hmwP3 {
  font-size: 4vw;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.45);
}
.hmwP4 {
  color: #eb6100;
  font-size: 4.53333vw;
}
/* li */
.hmw-center .hmwXQ {
  position: relative;
}
.hmwXQ .hmwNo {
  position: absolute;
  top: 0.5rem;
  right: 0.7rem;
  font-size: 1.2rem;
  color: #646464;
  font-weight: bold;
}
.hmwXQ .hmwYes {
  position: absolute;
  top: 0.5rem;
  right: 0.7rem;
  font-size: 1.2rem;
  color: #eb6100;
  font-weight: bold;
}
/*  */
.hmwTD > div {
  display: flex;
  flex-direction: column;
  font-size: 3.2vw;
  font-weight: 400;
  color: #595959;
  align-items: center;
  width: 4.5rem;
}
.hmwTD img {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 2rem;
}
.hmwPL img {
  width: 1.7rem;
  height: 1.7rem;
  border-radius: 1rem;
}
.hmwTD span {
  padding: 2.13333vw 0.8vw 0;
  padding-top: 1vw;
  padding-bottom: 1rem;
}
/*  */
.hmwDG ul > li {
  list-style: disc;
  color: #eb6100;
  margin: 0 2rem;
  margin-bottom: 1rem;
}
/* span */
.hmwS1 {
  padding: 0.2rem 0.4rem;
  font-size: 0.1rem;
  background: #ea7a2f;
  color: white;
  border-radius: 0.2rem;
  margin-right: 0.5rem;
}
.hmwS2 {
  font-size: 3.2vw;
  font-weight: 400;
  color: #595959;
}
.hmwDG .hmwP3 {
  font-size: 3.6vw;
  padding: 0.5rem;
}
/*  */
.hmwPL li {
  display: flex;
  justify-content: space-between;
  padding-left: 0.7rem;
  padding-bottom: 1rem;
}
.hmwPL-center .hmwCP1 {
  display: flex;
  flex-wrap: nowrap;
  font-size: 3.73333vw;
  font-weight: 400;
  color: #333;
}
.hmwPL-center .hmwCP1 span {
  margin-right: 0.5rem;
}
.hmwCP2 {
  padding: 0.8rem 0;
  line-height: 3.4vw;
  color: #999;
  font-size: 3.2vw;
}
.hmwPL-right {
  font-size: 3.2vw;
  font-weight: 400;
  color: #999;
  text-align: right;
}
.hmwPL .hmwP1 {
  padding-bottom: 0.5rem;
}
/*  */
.hmwPL .van-rate__item {
  padding-left: 0;
}
/*  -----------------------------------------------------------------------------------------*/
.hmw-foot .van-button {
  background: #eb6100;
  color: white;
  border: none;
  height: 2.8rem;
  font-size: 1.2rem;
}
.van-tabbar {
  height: 2.8rem;
}
/*  */
.van-popup {
  padding: 0.8rem 2.5rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}
.van-popup img {
  width: 11.5rem;
}
.van-popup p {
  text-align: center;
  margin-bottom: 1.5rem;
}
</style>
